.body {
    height: fit-content;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: radial-gradient( circle farthest-corner at 50.2% 50.7%,  rgba(241,168,179,1) 0%, rgba(254,246,233,1) 90% );
    padding-top: 15vh;
    min-height: 100vh;
}

.navbar {
    display: flex;
    width: 100vw;
    height: 10vh;
    align-items: center;
    justify-content: center;
    color: #E84545;
    background-color: #2B2E4A;
    position: fixed;
    top: 0;
    z-index: 20;
}

.title {
    font-size: 5vh;
    font-weight: bold;
    margin: 0;
}

@media (aspect-ratio < 1) {
    .title {
        font-size: 7vw;
    }
}

.logo {
    height: 5vh;
    margin-right: 1vw;
}

@media (aspect-ratio < 1) {
    .logo {
        height: 7vw;
    }
}

.modePanel {
    height: 5vh;
    width: 40vw;
    display: grid;
    grid-template-columns: 50% 50%;
    border: 2px solid #53354A;
    justify-content: center;
    align-items: center;
    margin-top: 0vh;
    user-select: none;
}

@media (aspect-ratio < 1) {
    .modePanel {
        width: 80vw;
    }
}

.modeOption {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5vh;
    margin: 0;
    color: #53354A;
}

@media (aspect-ratio < 1) {
    .modeOption{
        font-size: 5vw;
    }
}

.modeOption:hover {
    cursor: pointer;
}

.home {
    color: white;
    font-size: xx-large;
    position: fixed;
    top: 0;
    left: 0;
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 5vw;
    padding-right: 5vw;
    text-decoration: none;
    font-weight: bold;
}

@media (aspect-ratio < 1) {
    .home {
        font-size: 5vw;
    }
}

.home:hover {
    color: #2B2E4A;
    background-color: white;
}