
.body {
    border: 2px solid black;
    width: 50vw;
    padding-left: 2vw;
    padding-right: 2vw;
    margin-bottom: 4vh;
    padding-top: 3vh;
    padding-bottom: 3vh;
    border-radius: 1vh;
}

@media (aspect-ratio < 1) {
    .body {
        width: 80vw;
        padding-top: 1vh;
        padding-bottom: 1vh;
    }
}

.claimBox {
    display: grid;
    grid-template-columns: 70% 20% 10%;
    align-items: center;
}

@media (aspect-ratio < 1) {
    .claimBox {
        grid-template-columns: 60% 25% 15%;
    }
}

.claim_text {
    font-size: x-large;
}

@media (aspect-ratio < 1) {
    .claim_text {
        font-size: 3.5vw;
    }
}

.verdict {
    font-size: large;
    background-color: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 1vh;
    margin-left: 1vw;
    height: 5vh;
}

@media (aspect-ratio < 1) {
    .verdict {
        font-size: 3vw;
        margin-left: 0;
        text-align: center;
        padding: 1vw;
    }
}

.sourceBlock {
    transition : height 0.25s ease;
    display: none;
    flex-direction: column;
    height: 0;
}

.drop_down {
    height: 5vh;
    transform-origin: center;
    transition : transform 0.25s ease-in-out;
    display: flex;
    background-size: cover;
    padding-left: 2vw;
}

@media (aspect-ratio < 1) {
    .drop_down {
        height: 4vh;
    }
}

.drop_down:hover {
    cursor: pointer;
}

.sources {
    border: 2px solid black;
    margin-bottom: 1vh;
    padding-left: 1vw;
    padding-right: 1vw;
    border-radius: 1vh;
    background-color: #323232;
}

@media (aspect-ratio < 1) {
    .sources {
        font-size: 3vw;
    }
}

.evidence_title {
    font-size: x-large;
    color: white;
}

@media (aspect-ratio < 1) {
    .evidence_title {
        font-size: 3.5vw;
    }
}

.source_box {
    margin-bottom: 2vh;
    background-color: #5f5f5f;
    padding: 2vh;
    border-radius: 1vh;
}

.source_name {
    margin: 0;
    color: white;
}

.source_text {
    color: white;
}

.credibility_score {
    margin: 0;
    color: white;
}