.body {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row;
    width: 100vw;
    background-color: #522546;
    justify-content: space-between;
    padding-left: 10vw;
    padding-right: 5vw;
    box-sizing: border-box;
    height: 15vh;
    border-bottom: 4px solid #F7374F;
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 15vh;
    font-size: 5vh;
    margin: 0;
    color: #F7374F;
}

.backButton {
    color: #F7374F;
    font-size: xx-large;
    text-decoration: none;
    height: 15vh;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 2vw;
    padding-right: 2vw;
    font-weight: bold;
}

@media (aspect-ratio < 1) {
    .backButton {
        font-size: 5vw;
    }    
}

.backButton:hover {
    cursor: pointer;
    background-color: #F7374F;
    color: #522546;
}

.panel {
    display: flex;
    margin-top: 15vh;
}

@media (aspect-ratio < 1) {
    .panel {
        flex-direction: column;
        margin-top: 3vh;
    }
}

.card {
    border: 2px solid #F7374F;
    padding: 2vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 2vw;
    margin-right: 2vw;
    border-radius: 1vh;
    background-color: #2C2C2C;
    color: #FEBA17;
    transition: transform 0.5s ease;
}

@media (aspect-ratio < 1) {
    .card {
        margin-bottom: 5vh;
        font-size: 2vh;
        width: 60vw;
    }    
}

.card:hover {
    transform: scale(1.2);
}

.photo {
    width: 10vw;
    border-radius: 1vh;
}

@media (aspect-ratio < 1) {
    .photo {
        width: 40vw;
    }
}