.body {
    height: fit-content;
}

@media (aspect-ratio < 1) {
    .body {
        height: 100vh;
        box-sizing: border-box;
    }
}

.navbar {
    position: fixed;
    top: 0;
    height: 10vh;
    color: white;
    padding-left: 5vw;
    padding-right: 5vw;
    display: flex;
    align-items: center;
    border-bottom: 2px solid white;
    width: 100vw;
    display: grid;
    grid-template-columns: 80% auto auto;
    box-sizing: border-box;
}

@media (aspect-ratio < 1) {
    .navbar {
        display: flex;
        flex-direction: column;
        height: fit-content;
        box-sizing: border-box;
    }
}

.truthcreds {
    font-size: 4vh;
    padding: 0;
    margin: 0;
    user-select: none;
}

@media (aspect-ratio < 1) {
    .truthcreds {
        padding: 2vh;
    }
}

.option {
    font-size: 2.2vh;
    font-weight: bold;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 10vh;
    margin: 0;
    user-select: none;
    color: white;
    text-decoration: none;
}

@media (aspect-ratio < 1) {
    .option {
        border: 2px solid white;
        width: 100vw;
        height: 5vh;
    }
}

.option:hover {
    cursor: pointer;
    background-color: white;
    color: #261FB3;
    
}


.firstPage {
    height: 100vh;
    width: 100vw;
    background-image: url('homepage.jpg');
    background-size: cover;
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
    .firstPage {
        background-position: center;
    }
}

.tagline {
    color: white;
    background-color: white;
    color: #261FB3;
    padding: 2vw;
    font-size: 5vh;
    border-radius: 2vh;
    
}

@media (aspect-ratio < 1) {
    .tagline {
        font-size: 7vw;
        text-align: center;
        width: 100vw;
        border-radius: 0;
    }
}