@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.body {
    display: flex;
    height: 50vh;
    flex-direction: column;
    padding-top: 5vh;
    align-items: center;
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
  .title {
    font-size: 5vw;
  }
}

.input_block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
    .input_block {
        margin-top: 2vh;
    }   
}

.input_box {
    height: 20vh;
    width: 40vw;
    border: 2px solid black;
    font-size: larger;
    font-family: 'Poppins', sans-serif;
    padding: 1vh;
    margin-bottom: 5vh;
}

@media (aspect-ratio < 1) {
  .input_box {
    width: 80vw;
    font-size: 3vw;
  }
}

.input_box:focus {
    outline: none;
}

@media (aspect-ratio < 1) {
    .errorMsg {
        font-size: 5vw;
    }
}

.button {
  height: 5vh;
  font-size: large;
  width: 10vw;
  border: 2px solid #53354A;
  color: white;
  background-color: #53354A;
  transition: 0.3s ease;
  border-radius: 1vh;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

@media (aspect-ratio < 1) {
  .button {
    height: 5vh;
    width: 30vw;
    font-size: 5vw;
  }
}

.button:hover {
  color: #53354A;
  background-color: white;
  cursor: pointer;
  transition: 0.3s ease;
  height: 6vh;
  width: 11vw;
}

@media (aspect-ratio < 1) {
  .button:hover {
    height: 6vh;
    width: 31vw;
  }
}

.result_title {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* HTML: <div class="loader"></div> */
/* HTML: <div class="loader"></div> */
.loader {
    width: 5vw;
    aspect-ratio: 1;
    display: grid;
    border: 4px solid #0000;
    border-radius: 50%;
    border-color: #ccc #0000;
    animation: l16 1s infinite linear;
  }
  .loader::before,
  .loader::after {    
    content: "";
    grid-area: 1/1;
    margin: 2px;
    border: inherit;
    border-radius: 50%;
  }
  .loader::before {
    border-color: #f03355 #0000;
    animation: inherit; 
    animation-duration: .5s;
    animation-direction: reverse;
  }
  .loader::after {
    margin: 8px;
  }
  @keyframes l16 { 
    100%{transform: rotate(1turn)}
  }

.textIcon {
    height: 5vh;
    margin-right: 0.5vw;
}

@media (aspect-ratio < 1) {
  .textIcon {
    height: 10vw;
    margin-right: 3vw;
  }
}

@media (aspect-ratio < 1) {
    .loader {
        width: 30vw;
        font-size: 5vw;
    }

    .waiting {
        font-size: 5vw;
        text-align: center;
        padding-left: 10vw;
        padding-right: 10vw;
    }
}