.body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 2px solid #2B2E4A;
    margin-top: 5vh;
    background-color: #102E50;
    color: #F5C45E;
    border-radius: 3vh;
    margin-bottom: 5vh;
}

@media (aspect-ratio < 1) {
    .body {
        width: 100vw;
        font-size: 3vw;
        border-radius: 0;
        margin-bottom: 0;
    }
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 0;
}

.instructionBody {
    font-size: large;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 5vh;
}

@media (aspect-ratio < 1) {
    .instructionBody {
        width: 100vw;
        box-sizing: border-box;
        font-size: 4vw;
        text-align: center;
    }

    .page {
        width: 100vw;
    }
}

.instructionIcon {
    height: 4vh;
    margin-right: 0.5vw;
}

.instructionList {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

@media(aspect-ratio >= 1) {
    .page {
        height: 60vh;
    }
}
