
.body {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.results_title {
    font-size: 4vh;
    margin-bottom: 0;
}

.percentage {
    font-size: large;
    margin-top: 0;
}

@media (aspect-ratio < 1) {
  .percentage {
    font-size: 4vw;
  }
}

.loader {
    width: 5vw;
    aspect-ratio: 1;
    display: grid;
    border: 4px solid #0000;
    border-radius: 50%;
    border-color: #ccc #0000;
    animation: l16 1s infinite linear;
    margin-top: 20vh;
  }
  .loader::before,
  .loader::after {    
    content: "";
    grid-area: 1/1;
    margin: 2px;
    border: inherit;
    border-radius: 50%;
  }
  .loader::before {
    border-color: #f03355 #0000;
    animation: inherit; 
    animation-duration: .5s;
    animation-direction: reverse;
  }
  .loader::after {
    margin: 8px;
  }
  @keyframes l16 { 
    100%{transform: rotate(1turn)}
  }