@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

.body {
    display: flex;
    height: 50vh;
    flex-direction: column;
    align-items: center;
}

@media (aspect-ratio < 1) {
    .body {
        margin-top: 3vh;
    }
}

.title {
    display: flex;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
  .title {
    font-size: 5vw;
  }
}

.input_block {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
    .input_block {
        margin-top: 2vh;
    }   
}

.input_box {
    height: 5vh;
    width: 30vw;
    border: 2px solid black;
    font-size: large;
    padding: 1vh;
    margin-bottom: 5vh;
    font-family: 'Poppins', sans-serif;
    text-align: center;
}

@media (aspect-ratio < 1) {
    .input_box {
        height: 3vh;
        width: 60vw;
        font-size: 1.5vh;
    }
}

.input_box:focus {
    outline: none;
}

@media (aspect-ratio < 1) {
    .errorMsg {
        font-size: 5vw;
    }
}

@media (aspect-ratio < 1) {
    .limitExceeded {
        font-size: 5vw;
    }
}

.button {
    height: 5vh;
    font-size: large;
    width: 10vw;
    border: 2px solid #53354A;
    color: white;
    background-color: #53354A;
    transition: 0.3s ease;
    border-radius: 1vh;
    font-family: 'Poppins', sans-serif;
    font-weight: bold;
}

@media (aspect-ratio < 1) {
  .button {
    height: fit-content;
    width: fit-content;
    font-size: 5vw;
    padding: 2vw;
  }
}

.button:hover {
    color: #53354A;
    background-color: white;
    cursor: pointer;
    transition: 0.3s ease;
    height: 6vh;
    width: 11vw;
}

@media (aspect-ratio < 1) {
  .button:hover {
    height: fit-content;
    font-size: 6vw;
    width: fit-content;
  }
}

.lengthPanel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

@media (aspect-ratio < 1) {
    .lengthPanel {
        font-size: 5vw;
        padding-left: 10vw;
        padding-right: 10vw;
        width: 100vw;
        box-sizing: border-box;
        padding-bottom: 10vh;
    }
}

.timerPanel {
    display: flex;
    flex-direction: column;
    margin-top: 2vh;
    padding-top: 2vh;
}

.timerSubPanel {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 2vw;
    margin-right: 2vw;
    background-color: #2B2E4A;
    padding: 3vh;
    border-radius: 1vh;
    margin-bottom: 3vh;
}

@media (aspect-ratio < 1) {
    .timerSubPanel {
        font-size: 3vw;
    }
}

.timeBoxPanel {
    display: flex;
    font-size: large;
    font-weight: bold;
    color: #EAEAEA;
}

@media (aspect-ratio < 1) {
    .timeBoxPanel {
        font-size: 5vw;
    }
}

.timerLabel {
    margin-bottom: 2vh;
    font-size: x-large;
    font-weight: bold;
    color: #EAEAEA;
    width: 100%;
    padding-left: 3vw;
}

@media (aspect-ratio < 1) {
    .timerLabel {
        font-size: 5vw;
    }
}

.timeBox {
    height: 3vh;
    width: 3vw;
    font-size: large;
    border: 2px solid white;
    padding: 1vh;
    margin-left: 1vw;
    margin-right: 1vw;
    background-color: #2B2E4A;
    color: #EAEAEA;
    border-radius: 1vh;
}

@media (aspect-ratio < 1) {
    .timeBox {
        font-size: 5vw;
        width: 10vw;
    }
}

.timeBox:focus {
    outline: none;
}

.lengthText {
    margin: 0;
    font-size: 3vh;
}

@media (aspect-ratio < 1) {
    .lengthText {
        font-size: 5vw;
        text-align: center;        
    }
}

.loader {
    width: 5vw;
    aspect-ratio: 1;
    display: grid;
    border: 4px solid #0000;
    border-radius: 50%;
    border-color: #ccc #0000;
    animation: l16 1s infinite linear;
  }
  .loader::before,
  .loader::after {    
    content: "";
    grid-area: 1/1;
    margin: 2px;
    border: inherit;
    border-radius: 50%;
  }
  .loader::before {
    border-color: #f03355 #0000;
    animation: inherit; 
    animation-duration: .5s;
    animation-direction: reverse;
  }
  .loader::after {
    margin: 8px;
  }
  @keyframes l16 { 
    100%{transform: rotate(1turn)}
  }

.youtubeIcon {
    height: 5vh;
    margin-right: 0.5vw;
}

