}

@media (aspect-ratio < 1) {
    .loader {
        width: 30vw;
        font-size: 5vw;
    }

    .waiting {
        font-size: 5vw;
        text-align: center;
        padding-left: 10vw;
        padding-right: 10vw;
    }
}